<!--  UI  -->
<template>
  <v-card-text class="black--text font-weight-bord" style="background: #f8f3f0">
    <v-row class="font">
      <v-col>  </v-col>
      <v-col class="text-right"> {{ pprice == null ? 0 : String(pprice).replace(/\B(?=(\d{3})+(?!\d))/g, ",") }} </v-col>
    </v-row>
    <v-row class="font">
      <v-col>  </v-col>
      <v-col class="text-right"> {{ postp == null ? 0 : String(postp).replace(/\B(?=(\d{3})+(?!\d))/g, ",") }} </v-col>
    </v-row>
    <v-row class="mb-3">
      <v-col class="font">  </v-col>
      <v-col class="text-right textcolor"> -{{ discount == null ? 0 : String(discount).replace(/\B(?=(\d{3})+(?!\d))/g, ",") }} </v-col>
    </v-row>

    <v-divider class="black" />
    <v-row class="mt-3 just">
      <v-col v-if="content"> {{ content }} </v-col>
      <v-col v-else> {{ pamount == null ? 0 : pamount }} </v-col>
      <v-col class="text-right" style="font-size: 22px">
        {{
          String((pprice == null ? 0 : pprice) - (postp == null ? 0 : postp) - (discount == null ? 0 : discount)).replace(
            /\B(?=(\d{3})+(?!\d))/g,
            ",",
          )
        }}
      </v-col>
    </v-row>
  </v-card-text>
</template>

<script>
export default {
  // (devtools  )
  name: "Payment",
  //   
  components: {},
  //   
  data() {
    return {
      totoalprice: 0,
    };
  },
  //  
  methods: {},

  props: ["pprice", "pamount", "discount", "postp", "content"],
};
</script>

<!--    -->
<style scoped>
.just {
  color: black;
  font-weight: bold;
}
.font {
  font-size: 15px;
  color: black;
}
.textcolor {
  font-size: 15px;
  color: #c2935f;
}
</style>
