<!--  UI  -->
<template>
  <div>
    <div class="white black-txt pt-5" v-if="needHead">
      <v-row no-gutters class="orderfont">
        <v-col class="ml-5"> {{ new Date(date).toLocaleDateString().substring(0, 12) }} </v-col>
        <v-spacer></v-spacer>
        <v-col>
          <v-btn plain block class="orderfont" height="18" :to="url" v-if="url">  > </v-btn>
        </v-col>
      </v-row>
    </div>
    <v-card-text>
      <v-row style="background: #f5f5f5">
        <v-carousel
          :show-arrows="true"
          :show-arrows-on-hover="true"
          height="17%"
          :hide-delimiter-background="true"
          :hide-delimiters="true"
          class="pa-4"
          delimiter-icon="mdi-minus"
        >
          <v-carousel-item v-for="(product, i) in orderList" :key="product.cartno">
            <v-row align="center" justify="center" class="pb-2">
              <v-col class="body-2">
                <slot name="header">
                  <div class="font-weight-bold mb-1">
                    <span v-if="title">{{ title }}</span
                    ><span v-else> </span>{{ i + 1 }}
                  </div>
                </slot>
                <div class="font-weight-bold">
                  {{ product.pbrand }}
                </div>
                <div class="font-weight-regular mb-2">
                  {{ product.pname }}
                </div>
                <div class="font-weight-bold mb-1">{{ String(product.pprice).replace(/\B(?=(\d{3})+(?!\d))/g, ",") }}</div>
                <div class="caption grey--text">
                  <span class="pr-4"> : {{ product.cartsize }}</span
                  ><span> : {{ product.cartcolor }}</span>
                  <span class="ml-3"> : {{ product.cartamount }}</span>
                </div>
              </v-col>
              <v-col class="fill-height" cols="4">
                <v-img :src="product.imgurl1" width="100px"></v-img>
              </v-col>
            </v-row>
            <slot name="foot"> </slot>
          </v-carousel-item>
        </v-carousel>
      </v-row>
    </v-card-text>
  </div>
</template>

<script>
export default {
  // (devtools  )
  name: "Product",
  //   
  components: {},
  //   
  data() {
    return {};
  },
  //  
  methods: {},

  props: ["orderList", "title", "needHead", "date", "url"],
};
</script>

<!--    -->
<style scoped>
.active-tab {
  color: black;
  font-weight: bold;
  text-shadow: 2px 3px 2px gray;
}
.just {
  color: black;
  font-weight: bold;
  font-size: 18px;
}
.orderfont {
  color: black;
  font-size: 12px;
}
</style>
