<!--  UI  -->
<template>
  <v-dialog v-model="flag" persistent max-width="600px">
    <template v-slot:activator="{ on, attrs }">
      <v-btn :input-value="on" plain block class="mt-3 ml-5" height="15" v-bind="attrs" v-on="on"> +    </v-btn>
    </template>
    <v-card>
      <v-card-title>
        <span class="font-weight-bold text-h5">  </span>
      </v-card-title>
      <v-card-text>
        <v-container>
          <v-row no-gutters>
            <v-col cols="12" sm="6" md="4">
              <v-text-field label="()" required></v-text-field>
            </v-col>
            <v-col cols="12" sm="6" md="4">
              <v-text-field type="number" label="()" required></v-text-field>
            </v-col>
            <v-col cols="12" md="4">
              <v-text-field label=" " v-model="address" required></v-text-field>
              <v-btn class="w-button3" @click="openDaumPostcode"> </v-btn>
            </v-col>
          </v-row>
        </v-container>
      </v-card-text>
      <div ref="wrap" style="display: none; border: 1px solid; width: 320px; height: 300px; margin: 5px 0; position: relative">
        <v-img
          src="//t1.daumcdn.net/postcode/resource/images/close.png"
          style="cursor: pointer; position: absolute; right: 0px; top: -1px; z-index: 1"
          @click="foldDaumPostcode"
          alt=" "
        />
      </div>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="blue darken-1" text @click="close">  </v-btn>
        <v-btn color="blue darken-1" text @click="$emit('close')">  </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script>
export default {
  // (devtools  )
  name: "Add",
  //   
  components: {},
  //   
  data() {
    return {
      flag: false,
    };
  },
  //  
  methods: {
    close() {
      this.flag = false;
    },
    openDaumPostcode() {
      const elementWrap = this.$refs.wrap;
      //  scroll  .
      const currentScroll = Math.max(document.body.scrollTop, document.documentElement.scrollTop);
      new window.daum.Postcode({
        oncomplete: (data) => {
          //       .

          //       .
          //      ('') ,    .
          let addr = ""; //  

          //         .
          if (data.userSelectedType === "R") {
            //     
            addr = data.roadAddress;
          } else {
            //     (J)
            addr = data.jibunAddress;
          }

          //      .
          // document.getElementById('address').value = addr
          this.address = addr;

          // iframe  element  .
          // (autoClose:false  ,      .)
          elementWrap.style.display = "none";

          //      scroll  .
          document.body.scrollTop = currentScroll;
        },
        //         . iframe  element  .
        onresize: function (size) {
          elementWrap.style.height = size.height + "px";
        },
        width: "100%",
        height: "100%",
      }).embed(elementWrap);
      elementWrap.style.display = "block";
    },
    foldDaumPostcode() {
      const elementWrap = this.$refs.wrap;
      elementWrap.style.display = "none";
    },
  },
};
</script>

