<!--  UI  -->
<template>
  <v-card>
    <v-card-title class="active-tab text-h5"></v-card-title>
    <v-divider class="grey lighten-3" />
    <v-card-text class="black--text">
      <v-row>
        <v-col>
          <v-btn icon @click="checkAll">
            <v-icon v-if="allcheck">mdi-check-circle</v-icon>
            <v-icon v-else>mdi-check-circle-outline</v-icon>
          </v-btn>
           
        </v-col>
        <v-col class="text-right">
          <!-- <v-btn text>  </v-btn> -->
        </v-col>
      </v-row>
    </v-card-text>

    <v-divider class="grey lighten-3" />
    <v-card-text style="background: #fafafa">
      <v-container class="just">
        <div v-for="(product, i) in orderList" :key="i">
          <v-row class="pa-3">
            <v-row no-gutters>
              <div v-if="product.soldOut"> .</div>
              <v-btn v-else fab class="pa-0 ma-0" style="background: #f5f5f5" depressed small @click="checked(i)">
                <v-icon small class="pa-0 ma-0" v-if="checkList[i]"> mdi-check-circle </v-icon>
                <v-icon small class="pa-0 ma-0" v-else> mdi-check-circle-outline </v-icon>
              </v-btn>
              <v-spacer></v-spacer>
              <v-btn fab depressed style="background: #f5f5f5" class="pa-0 ma-0" small @click="deleteCart(product.cartno)">
                <v-icon small color="red" class="pa-0 ma-0"> mdi-close </v-icon>
              </v-btn>
            </v-row>
            <v-row align="center" justify="center" class="pb-2 mt-0" no-gutters>
              <v-col class="body-2">
                <div class="font-weight-bold mb-1">   {{ i + 1 }}</div>
                <div class="font-weight-bold">
                  {{ product.pbrand }}
                </div>
                <div class="font-weight-regular mb-2">
                  {{ product.pname }}
                </div>
                <div class="font-weight-bold mb-1">{{ String(product.pprice * product.cartamount).replace(/\B(?=(\d{3})+(?!\d))/g, ",") }}</div>
                <div class="caption grey--text font-weight-bold">
                  <span class="pr-4"> : {{ product.cartsize }}</span
                  ><span class="mr-2"> : {{ product.cartcolor }}</span>
                  <span class="ml-3"> : {{ product.cartamount }}</span>
                </div>
              </v-col>
              <v-col class="fill-height" cols="4">
                <v-img :src="product.imgurl1" width="100px"></v-img>
              </v-col>
            </v-row>
            <!--   -->
            <v-row justify="center" class="pa-0 ma-0">
              <Bar
                :pname="product.pname"
                :pbrand="product.pbrand"
                :pamount="product.cartamount"
                :pcode="product.pcode"
                :pcolor="product.cartcolor"
                :psize="product.cartsize"
                :cartno="product.cartno"
                @datachange="cartlist($store.getters.getMno)"
                @close="dialogClose"
                @isSoldOut="isSoldOut"
              />
            </v-row>
          </v-row>
        </div>
      </v-container>
    </v-card-text>
    <v-divider />
    <v-card-text class="black--text" style="background: #f8f3f0">
      <v-container>
        <p class="just text-h6">  </p>
        <Payment :pamount="selectamount" :pprice="selectprice" />
      </v-container>
    </v-card-text>

    <v-card-text class="">
      <v-container>
        <div class="txt-list">
          <li>   3  . 3    .</li>
          <li>  300   ,        .</li>
          <li>        .</li>
          <li>    [  ] .</li>
        </div>
      </v-container>
    </v-card-text>
    <v-divider class="grey lighten-5 mt-2" />
    <order-footer :just="just">
      <template v-slot:click>
        <v-btn block style="background: #c2935f" @click="newOrder">
          <v-icon class="just" color="white"> </v-icon>
        </v-btn>
      </template>
    </order-footer>
    <snack-bar :content="'  !'" :snackbar="snackbar" @close="snackbar = false" />
  </v-card>
</template>

<script>
import Bar from "@/components/tool/Bar.vue";
import Payment from "@/components/order/Payment.vue";
import cartapi from "@/apis/cart";
import OrderFooter from "@/components/footer/OrderFooter.vue";
import SnackBar from "@/components/tool/SnackBar.vue";

export default {
  // (devtools  )
  name: "Cart",
  //   
  components: { Bar, Payment, OrderFooter, SnackBar },
  //   
  data() {
    return {
      snackbar: false,
      just: true,
      flag: false,
      scrollPosition: 0,
      beforePosition: 0,
      selectamount: 0,
      selectprice: 0,
      payment: {
        price: "20000",
        delivery: "0",
        discount: "-10000",
        total: "10000",
        count: "1",
      },
      orderList: [],
      checkList: [],
      allcheck: false,
    };
  },
  //  
  methods: {
    newOrder() {
      let checkedCart = [];
      this.checkList.forEach((item, idx) => {
        if (item) {
          if (!this.orderList[idx].soldOut) {
            checkedCart.push(this.orderList[idx].cartno);
          }
        }
      });

      if (checkedCart.length < 1) {
        this.snackbar = true;
        return;
      }

      console.log(checkedCart);
      //   cartno, selectamount() , selectprice()  .
      this.$router.push({ path: "/order/orderForm", query: { cartno: checkedCart, selectamount: this.selectamount, selectprice: this.selectprice } });
    },
    dialogClose() {
      console.log("run");
    },
    //  
    async cartlist(mno) {
      try {
        const promise = await cartapi.getcartlist(mno);
        this.orderList = promise.data.cart;
        this.checkList = [];
        this.allcheck = false;
        for (let i = 0; i < this.orderList.length; i++) {
          this.checkList.push(false);
        }
      } catch (error) {
        console.log(error);
      }
    },
    //  
    async deleteCart(cartno) {
      try {
        const promise = await cartapi.deleteCart({ cartno, mno: this.$store.getters.getMno });
        if (promise.data == "success") {
          this.cartlist(this.$store.getters.getMno);
        } else {
          console.log("");
        }
      } catch (error) {
        console.log(error);
      }
    },
    // 
    handleScroll(e) {
      this.scrollPosition = e.target.documentElement.scrollTop;
      if (this.scrollPosition == 0 || this.beforePosition <= this.scrollPosition) {
        this.just = true;
      } else {
        this.just = false;
      }
      this.beforePosition = this.scrollPosition;
    },
    //
    checked(val) {
      if (this.checkList[val]) {
        this.allcheck = false;
      }
      this.$set(this.checkList, val, !this.checkList[val]);
    },
    checkAll() {
      this.allcheck = !this.allcheck;
      this.checkList.forEach((item, idx) => {
        this.$set(this.checkList, idx, this.allcheck);
      });
    },
    //     
    isSoldOut(cartno, amount, pamount) {
      for (let o of this.orderList) {
        if (o.cartno == cartno) {
          o["soldOut"] = pamount > amount;
          break;
        }
      }
    },
  },
  created() {
    if (!this.$store.getters.getMno || this.$store.getters.getMno == null) {
      this.$router.push("/member/login");
    }

    this.cartlist(this.$store.getters.getMno);
    this.deleteCart();
    window.addEventListener("scroll", this.handleScroll);
  },
  mounted() {
    window.scrollTo(0, 0);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
  //
  watch: {
    checkList: function (list) {
      //
      let cnt = 0;
      // 
      let p = 0;
      list.forEach((item, idx) => {
        if (item) {
          const obj = this.orderList[idx];
          if (!obj.soldOut) {
            cnt += 1;
            p += obj.cartamount * obj.pprice;
          }
        }
      });
      this.selectamount = cnt;
      this.selectprice = p;
    },
  },
};
</script>
<!--    -->
<style scoped>
.active-tab {
  color: black;
  font-weight: bold;
  text-shadow: 1px 1px 1px gray;
}
.just {
  color: black;
  font-weight: bold;
  font-size: 20px;
}
.simple {
  color: black;
}
.main-txt {
  color: #c2935f;
}

.bg-gray {
  background-color: #f5f5f5;
}
</style>
