<!--  UI  -->
<template>
  <v-card>
    <v-card-title class="active-tab text-h4"> </v-card-title>
    <v-divider />

    <v-tabs fixed-tabs active-class="gotab" slider-color="black" v-model="tab" color="black">
      <v-tab>15</v-tab>
      <v-tab>1</v-tab>
      <v-tab>3</v-tab>
    </v-tabs>

    <v-tabs-items v-model="tab">
      <v-tab-item v-for="idx in 3" :key="idx">
        <div v-for="orderSheet in orderSheetList[idx - 1][0]" :key="orderSheet.ono">
          <div v-for="list in orderProductListByOno[idx - 1]" :key="list.ono">
            <div v-if="list">
              <Product
                :orderList="list.list"
                class="white black-txt pt-6"
                :title="' '"
                :needHead="true"
                :date="orderSheet.oregdate"
                :url="'/order/ProductDetails?ono=' + orderSheet.ono"
                v-if="orderSheet.ono == list.ono"
              />
            </div>
          </div>
        </div>
      </v-tab-item>
    </v-tabs-items>
    <!--  -->
    <div class="pt-6">
      <v-card class="mx-auto" max-width="344">
        <!--max-width="720px" aspect-ratio="16:9" thumbnail-quality="standard"    -->
        <LazyYoutube ref="youtubeLazyVideo" :src="youtubeLink" />

        <v-card-title> LKL Fashion Mall </v-card-title>

        <v-card-subtitle class="pt-2"> LKL Fashion Mall  . </v-card-subtitle>

        <v-card-actions>
          <v-btn color="orange lighten-2" text> Please </v-btn>

          <v-spacer></v-spacer>

          <v-btn icon @click="show = !show">
            <v-icon>{{ show ? "mdi-chevron-up" : "mdi-chevron-down" }}</v-icon>
          </v-btn>
        </v-card-actions>

        <v-expand-transition>
          <div v-show="show">
            <v-divider></v-divider>

            <v-card-text> 4 !  </v-card-text>
          </div>
        </v-expand-transition>
      </v-card>
    </div>
  </v-card>
</template>

<script>
import Product from "@/components/order/Product.vue";
import OrderApi from "@/apis/order";
import ProductApi from "@/apis/product";
import { LazyYoutube } from "vue-lazytube";
export default {
  // (devtools  )
  name: "",
  //   
  components: { LazyYoutube, Product },
  //   
  data() {
    return {
      //YouTube            .     YouTube     .
      //10       vue-lazytube   7,   2-3 .
      youtubeLink: "https://www.youtube.com/watch?v=GL9Blq2gRhE",
      show: false,
      orderSheetList: [[], [], []],
      orderProductListByOno: { 0: [], 1: [], 2: [] },
      tab: 0,
      date: new Date().toLocaleDateString(),
    };
  },
  //  
  methods: {
    go1Url() {
      this.$router.push("/order/ProductDetails");
    },
    async orderProduct(tempOrderProduct, ono) {
      //  orderSheet orderList 

      try {
        const promise = await OrderApi.getOrderSheet(this.$store.getters.getMno, ono);
        const orderproductlist = promise.data.orderproductlist;
        // let temp = {};
        // for (let key in this.orderProductListByOno[idx]) {
        //   if (this.orderProductListByOno[idx][key].ono == ono) {
        //     temp = this.orderProductListByOno[idx][key].ono;
        //     break;
        //   }
        // }
        // console.log("temp : " + temp); // orderno
        // console.log(orderproductlist); //  

        for (let key in orderproductlist) {
          const productdetail = await ProductApi.getProductDetail(orderproductlist[key].pcode);
          for (let colorkey in productdetail.data.color) {
            if (productdetail.data.color[colorkey].pcolor == orderproductlist[key].pcolor) {
              let obj = {
                pbrand: productdetail.data.pbrand,
                pname: orderproductlist[key].pname,
                cartsize: orderproductlist[key].psize,
                cartcolor: orderproductlist[key].pcolor,
                cartamount: orderproductlist[key].opamount,
                imgurl1: productdetail.data.color[colorkey].imgurl1,
                cartno: orderproductlist[key].opno,
                pprice: productdetail.data.pprice * orderproductlist[key].opamount,
              };
              for (let temp in tempOrderProduct) {
                if (tempOrderProduct[temp].ono == ono) {
                  if (tempOrderProduct[temp]["list"]) {
                    tempOrderProduct[temp]["list"].push(obj);
                  } else {
                    tempOrderProduct[temp]["list"] = [];
                    tempOrderProduct[temp]["list"].push(obj);
                  }
                  break;
                }
              }

              break;
            }
          }
        }
      } catch (error) {
        console.log(error);
      }
    },

    async getOrderSheets(val) {
      try {
        let tempList = [];
        let tempOrderProduct = [];

        let day = 0;
        if (val == 0) {
          day = 15;
        } else if (val == 1) {
          day = 30;
        } else {
          day = 90;
        }
        let temp = [];
        const promise = await OrderApi.getOrderSheetList(this.$store.getters.getMno, day);

        for (let os in promise.data) {
          temp.push(promise.data[os]);
          tempOrderProduct.push({ ono: promise.data[os].ono });

          await this.orderProduct(tempOrderProduct, promise.data[os].ono);
        }
        tempList.push(temp);

        this.$set(this.orderProductListByOno, val, tempOrderProduct);
        this.$set(this.orderSheetList, val, tempList);
      } catch (error) {
        console.log(error);
      }
    },
  },
  created() {
    this.getOrderSheets(0);
    this.getOrderSheets(1);
    this.getOrderSheets(2);
  },
  mounted() {
    window.scrollTo(0, 0);
  },
  watch: {},
};
</script>

<!--    -->
<style scoped>
.active-tab {
  color: black;
  font-weight: bold;
  text-shadow: 1px 1px 1px gray;
}
.gotab {
  background-color: rgb(238, 214, 178);
  font-weight: bold;
}
.orderfont {
  color: black;
  font-size: 13px;
}
</style>
